
'use client';

import { useState, useEffect, useCallback, useRef } from 'react';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Check, Star, Loader2 } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { usePageBackground } from '@/hooks/usePageBackground';
import { Badge } from '@/components/ui/badge';
import { useRouter } from 'next/navigation';
import { Skeleton } from '@/components/ui/skeleton';
import { useToast } from '@/hooks/use-toast';
import { Button } from '@/components/ui/button';
import { Alert, AlertTitle, AlertDescription } from '@/components/ui/alert';

const includedFeatures = [
  'Unlimited Practice Tests',
  'All Difficulty Levels',
  'Detailed Progress Tracking',
  'No Advertisements',
  'Priority Support',
];

const RazorpaySubscriptionButton = ({ planId, customerId, user }: { planId: string; customerId: string; user: any; }) => {
  const formRef = useRef<HTMLFormElement>(null);

  useEffect(() => {
    const form = formRef.current;
    if (!form || !user || !customerId) return;

    // Clear previous buttons to prevent duplicates
    form.innerHTML = '';
    
    const script = document.createElement('script');
    script.src = "https://cdn.razorpay.com/static/widget/subscription-button.js";
    script.async = true;
    script.setAttribute('data-subscription_button_id', planId);
    script.setAttribute('data-customer_id', customerId);

    const notes = { firebase_uid: user.uid };
    script.setAttribute('data-notes', JSON.stringify(notes));
    
    form.appendChild(script);

    return () => {
      if (form && form.contains(script)) {
        try {
          form.removeChild(script);
        } catch (e) {
          console.warn("Could not remove Razorpay script on cleanup.");
        }
      }
    };
  }, [planId, user, customerId]);

  return <form ref={formRef} />;
};


export default function PricingPage() {
  usePageBackground('');
  const { user, profile, isLoading, fetchProfile, createRazorpayCustomer } = useAuth();
  const router = useRouter();
  const { toast } = useToast();
  
  const [error, setError] = useState<string | null>(null);
  const [isCreatingCustomer, setIsCreatingCustomer] = useState(false);
  const [showPaymentButton, setShowPaymentButton] = useState(false);

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
    }
  }, [isLoading, user, router]);

  const handleCreateCustomer = useCallback(async () => {
    if (!user || !profile) return;
    
    // If customer ID already exists, just show the button
    if (profile.razorpayCustomerId) {
        setShowPaymentButton(true);
        return;
    }

    setIsCreatingCustomer(true);
    setError(null);
    try {
      await createRazorpayCustomer();
      setShowPaymentButton(true); // This will trigger re-render, and profile will have the ID
    } catch (e: any) {
      setError(e.message || 'Could not initialize payment. Please try again.');
      toast({
        title: "Payment Setup Failed",
        description: e.message || "An unknown error occurred.",
        variant: "destructive",
      });
    } finally {
      setIsCreatingCustomer(false);
    }
  }, [user, profile, createRazorpayCustomer, toast]);

  useEffect(() => {
    const handleRazorpaySuccess = async (event: any) => {
      if (!event.detail || !event.detail.razorpay_subscription_id) return;
      
      toast({
        title: "Payment Successful!",
        description: "Welcome to Pro! Your account is being upgraded. This may take a moment...",
      });

      let attempts = 0;
      const maxAttempts = 10;
      const pollProfile = async () => {
        if (!user) return;
        attempts++;
        try {
          const updatedProfile = await fetchProfile(user);
          if (updatedProfile?.subscriptionStatus === 'pro') {
            toast({ title: "Upgrade Complete!", description: "You now have full access to Abacus Pro." });
          } else if (attempts < maxAttempts) {
            setTimeout(pollProfile, 3000);
          } else {
             toast({ title: "Upgrade Processing", description: "Your upgrade is taking longer than expected. Please refresh if you don't see the change soon." });
          }
        } catch (error) {
           if (attempts < maxAttempts) setTimeout(pollProfile, 3000);
        }
      };
      setTimeout(pollProfile, 3000);
    };

    window.addEventListener('razorpay.subscription.subscribed', handleRazorpaySuccess);
    return () => {
      window.removeEventListener('razorpay.subscription.subscribed', handleRazorpaySuccess);
    };
  }, [user, toast, fetchProfile]);
  

  if (isLoading || !user || !profile) {
    return (
        <div className="max-w-4xl mx-auto">
            <div className="text-center mb-12"> <Skeleton className="h-12 w-3/4 mx-auto" /> <Skeleton className="h-6 w-1/2 mx-auto mt-4" /> </div>
            <div className="flex justify-center px-4 sm:px-0"> <Skeleton className="h-[500px] w-full max-w-md" /> </div>
      </div>
    );
  }

  const isAlreadyPro = profile?.subscriptionStatus === 'pro' || 
                    (profile?.subscriptionStatus === 'cancelled' && 
                     profile.accessExpiresAt && new Date() < new Date(profile.accessExpiresAt));

  return (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold tracking-tight text-foreground font-headline sm:text-5xl"> Unlock Your Full Potential </h1>
        <p className="mt-4 max-w-2xl mx-auto text-lg text-muted-foreground"> Choose the plan that's right for you and start mastering mental math today. </p>
      </div>
      
      <div className="flex justify-center px-4 sm:px-0">
        <Card className="w-full max-w-md shadow-2xl border-2 border-primary md:scale-105">
           <CardHeader className="text-center bg-primary/10 p-6">
                <Star className="w-12 h-12 text-primary mx-auto mb-4" />
                <CardTitle className="text-3xl font-bold">Abacus Pro</CardTitle>
                <CardDescription className="text-lg"> Everything you need to become a math whiz. </CardDescription>
                <div className="mt-4 flex flex-col items-center gap-2">
                  <div className="flex items-baseline gap-4">
                    <p className="text-2xl font-semibold text-muted-foreground line-through">₹799</p>
                    <Badge variant="destructive" className="text-base h-fit py-1 px-3 -rotate-12 transform">55% OFF</Badge>
                  </div>
                  <p className="text-5xl font-extrabold tracking-tight text-foreground">₹360<span className="text-base font-medium text-muted-foreground">/month</span></p>
                </div>
           </CardHeader>
           <CardContent className="p-6 md:p-8">
                <ul className="space-y-4">
                    {includedFeatures.map((feature) => ( <li key={feature} className="flex items-center gap-3"> <Check className="h-6 w-6 text-green-500" /> <span className="text-muted-foreground">{feature}</span> </li> ))}
                </ul>
           </CardContent>
           <CardFooter>
             <div className="w-full flex justify-center flex-col items-center gap-4">
                {isAlreadyPro ? (
                  <p className="w-full text-center text-primary font-semibold">You are already a Pro member!</p>
                ) : showPaymentButton && profile.razorpayCustomerId ? (
                  <>
                    <p className="text-xs text-muted-foreground">Customer ID: {profile.razorpayCustomerId}</p>
                    <RazorpaySubscriptionButton planId="pl_RDsiJqCv0SUHNV" customerId={profile.razorpayCustomerId} user={user} />
                  </>
                ) : (
                  <Button onClick={handleCreateCustomer} disabled={isCreatingCustomer} className="w-full">
                    {isCreatingCustomer ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                    Subscribe Now
                  </Button>
                )}
                
                {error && ( <Alert variant="destructive" className="mt-4"> <AlertTitle>Error</AlertTitle> <AlertDescription> {error} </AlertDescription> </Alert> )}
             </div>
           </CardFooter>
        </Card>
      </div>
    </div>
  )
}
